@import 'variables';

//colors
$black:  black;
$white: #fff;

/* Grays */
$color_athens_gray_approx: #ecf0f1;
$color_submarine_approx: #bdc3c7;
$color_silver_chalice_approx: #aaa;
$color_mirage_approx: #1b2129;

/* Primary */
$color_pickled_bluewood_approx: $primary;
$color_mountain_meadow_approx: #1abc9c;

$radius: $btn-border-radius;

//@extend-elements
//original selectors
//.ui-datepicker .ui-datepicker-prev span, .ui-datepicker .ui-datepicker-next span
%extend_1 {
	display: block;
	position: absolute;
	left: 50%;
	margin-left: -8px;
	top: 50%;
	margin-top: -8px;
}


.ui-helper-hidden {
	display: none;
}
.ui-helper-hidden-accessible {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}
.ui-helper-reset {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	line-height: 1.3;
	text-decoration: none;
	font-size: 100%;
	list-style: none;
}
.ui-helper-clearfix {
	min-height: 0;
	&:before {
		content: "";
		display: table;
		border-collapse: collapse;
	}
	&:after {
		content: "";
		display: table;
		border-collapse: collapse;
		clear: both;
	}
}
.ui-helper-zfix {
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	position: absolute;
	opacity: 0;
	filter: Alpha(Opacity=0);
}
.ui-front {
	z-index: 100;
}
.ui-state-disabled {
	cursor: default !important;
	opacity: .35;
	filter: Alpha(Opacity=35);
	background-image: none;
	.ui-icon {
		filter: Alpha(Opacity=35);
	}
}
.ui-icon {
	display: block;
	text-indent: -99999px;
	overflow: hidden;
	background-repeat: no-repeat;
	width: 16px;
	height: 16px;
	background-image: url(images/ui-icons_222222_256x240.png);
}
.ui-widget-overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: $color_pickled_bluewood_approx url(images/ui-bg_diagonal-maze_0_2c3e50_10x10.png) 50% 50% repeat;
	opacity: .9;
	filter: Alpha(Opacity=90);
}
.ui-draggable-handle {
	-ms-touch-action: none;
	touch-action: none;
}
.ui-resizable {
	position: relative;
}
.ui-resizable-handle {
	position: absolute;
	font-size: 0.1px;
	display: block;
	-ms-touch-action: none;
	touch-action: none;
}
.ui-resizable-n {
	cursor: n-resize;
	height: 7px;
	width: 100%;
	top: -5px;
	left: 0;
}
.ui-resizable-s {
	cursor: s-resize;
	height: 7px;
	width: 100%;
	bottom: -5px;
	left: 0;
}
.ui-resizable-e {
	cursor: e-resize;
	width: 7px;
	right: -5px;
	top: 0;
	height: 100%;
}
.ui-resizable-w {
	cursor: w-resize;
	width: 7px;
	left: -5px;
	top: 0;
	height: 100%;
}
.ui-resizable-se {
	cursor: se-resize;
	width: 12px;
	height: 12px;
	right: 1px;
	bottom: 1px;
}
.ui-resizable-sw {
	cursor: sw-resize;
	width: 9px;
	height: 9px;
	left: -5px;
	bottom: -5px;
}
.ui-resizable-nw {
	cursor: nw-resize;
	width: 9px;
	height: 9px;
	left: -5px;
	top: -5px;
}
.ui-resizable-ne {
	cursor: ne-resize;
	width: 9px;
	height: 9px;
	right: -5px;
	top: -5px;
}
.ui-selectable {
	-ms-touch-action: none;
	touch-action: none;
}
.ui-selectable-helper {
	position: absolute;
	z-index: 100;
	border: 1px dotted $black;
}
.ui-sortable-handle {
	-ms-touch-action: none;
	touch-action: none;
}
.ui-accordion {
	.ui-accordion-header {
		display: block;
		cursor: pointer;
		position: relative;
		margin: 2px 0 0;
		padding: .5em .5em .5em .7em;
		min-height: 0;
		font-size: 100%;
		.ui-accordion-header-icon {
			position: absolute;
			left: .5em;
			top: 50%;
			margin-top: -8px;
		}
	}
	.ui-accordion-icons {
		padding-left: 2.2em;
		.ui-accordion-icons {
			padding-left: 2.2em;
		}
	}
	.ui-accordion-content {
		padding: 1em 2.2em;
		border-top: 0;
		overflow: auto;
	}
}
.ui-autocomplete {
	position: absolute;
	top: 0;
	left: 0;
	cursor: default;
}
.ui-button {
	display: inline-block;
	position: relative;
	padding: 0;
	line-height: normal;
	margin-right: .1em;
	cursor: pointer;
	vertical-align: middle;
	text-align: center;
	overflow: visible;
	text-decoration: none;
	&:link {
		text-decoration: none;
	}
	&:visited {
		text-decoration: none;
	}
	&:hover {
		text-decoration: none;
	}
	&:active {
		text-decoration: none;
	}
	.ui-button-text {
		display: block;
		line-height: normal;
	}
}
.ui-button-icon-only {
	width: 2.2em;
	.ui-button-text {
		padding: .4em;
		text-indent: -9999999px;
	}
	.ui-icon {
		position: absolute;
		top: 50%;
		margin-top: -8px;
		left: 50%;
		margin-left: -8px;
	}
}
button {
	&.ui-button-icon-only {
		width: 2.4em;
	}
	&.ui-button-icons-only {
		width: 3.7em;
	}
	&.ui-button::-moz-focus-inner {
		border: 0;
		padding: 0;
	}
}
.ui-button-icons-only {
	width: 3.4em;
	.ui-button-text {
		padding: .4em;
		text-indent: -9999999px;
	}
	.ui-icon {
		position: absolute;
		top: 50%;
		margin-top: -8px;
	}
	.ui-button-icon-primary {
		left: .5em;
	}
	.ui-button-icon-secondary {
		right: .5em;
	}
}
.ui-button-text-icon-primary {
	.ui-button-text {
		padding: .4em 1em .4em 2.1em;
	}
	.ui-icon {
		position: absolute;
		top: 50%;
		margin-top: -8px;
	}
	.ui-button-icon-primary {
		left: .5em;
	}
}
.ui-button-text-icons {
	.ui-button-text {
		padding: .4em 1em .4em 2.1em;
		padding: .4em 2.1em .4em 1em;
		padding-left: 2.1em;
		padding-right: 2.1em;
	}
	.ui-icon {
		position: absolute;
		top: 50%;
		margin-top: -8px;
	}
	.ui-button-icon-primary {
		left: .5em;
	}
	.ui-button-icon-secondary {
		right: .5em;
	}
}
.ui-button-text-icon-secondary {
	.ui-button-text {
		padding: .4em 2.1em .4em 1em;
	}
	.ui-icon {
		position: absolute;
		top: 50%;
		margin-top: -8px;
	}
	.ui-button-icon-secondary {
		right: .5em;
	}
}
input {
	&.ui-button {
		padding: .4em 1em;
	}
	&.ui-button::-moz-focus-inner {
		border: 0;
		padding: 0;
	}
}
.ui-buttonset {
	margin-right: 7px;
	.ui-button {
		margin-left: 0;
		margin-right: -.3em;
	}
}
.ui-datepicker {
	width: 17em;
	padding: .2em .2em 0;
	display: none;
	.ui-datepicker-header {
		position: relative;
		padding: .2em 0;
	}
	.ui-datepicker-prev {
		position: absolute;
		top: 2px;
		width: 1.8em;
		height: 1.8em;
		left: 2px;
		span {
			@extend %extend_1;
		}
	}
	.ui-datepicker-next {
		position: absolute;
		top: 2px;
		width: 1.8em;
		height: 1.8em;
		right: 2px;
		span {
			@extend %extend_1;
		}
	}
	.ui-datepicker-prev-hover {
		top: 1px;
		left: 1px;
	}
	.ui-datepicker-next-hover {
		top: 1px;
		right: 1px;
	}
	.ui-datepicker-title {
		margin: 0 2.3em;
		line-height: 1.8em;
		text-align: center;
		select {
			font-size: 1em;
			margin: 1px 0;
		}
	}
	select {
		&.ui-datepicker-month {
			width: 45%;
		}
		&.ui-datepicker-year {
			width: 45%;
		}
	}
	table {
		width: 100%;
		font-size: .9em;
		border-collapse: collapse;
		margin: 0 0 .4em;
	}
	th {
		padding: .7em .3em;
		text-align: center;
		font-weight: bold;
		border: 0;
	}
	td {
		border: 0;
		padding: 1px;
		span {
			display: block;
			padding: .2em;
			text-align: right;
			text-decoration: none;
		}
		a {
			display: block;
			padding: .2em;
			text-align: right;
			text-decoration: none;
		}
	}
	.ui-datepicker-buttonpane {
		background-image: none;
		margin: .7em 0 0 0;
		padding: 0 .2em;
		border-left: 0;
		border-right: 0;
		border-bottom: 0;
		button {
			float: right;
			margin: .5em .2em .4em;
			cursor: pointer;
			padding: .2em .6em .3em .6em;
			width: auto;
			overflow: visible;
			&.ui-datepicker-current {
				float: left;
			}
		}
	}
	&.ui-datepicker-multi {
		width: auto;
	}
}
.ui-datepicker-multi {
	.ui-datepicker-group {
		float: left;
		table {
			width: 95%;
			margin: 0 auto .4em;
		}
	}
	.ui-datepicker-buttonpane {
		clear: left;
	}
	.ui-datepicker-group-last .ui-datepicker-header {
		border-left-width: 0;
	}
	.ui-datepicker-group-middle .ui-datepicker-header {
		border-left-width: 0;
	}
}
.ui-datepicker-row-break {
	clear: both;
	width: 100%;
	font-size: 0;
}
.ui-datepicker-rtl {
	direction: rtl;
	.ui-datepicker-prev {
		right: 2px;
		left: auto;
		&:hover {
			right: 1px;
			left: auto;
		}
	}
	.ui-datepicker-next {
		left: 2px;
		right: auto;
		&:hover {
			left: 1px;
			right: auto;
		}
	}
	.ui-datepicker-buttonpane {
		clear: right;
		button {
			float: left;
			&.ui-datepicker-current {
				float: right;
			}
		}
	}
	.ui-datepicker-group {
		float: right;
	}
	.ui-datepicker-group-last .ui-datepicker-header {
		border-right-width: 0;
		border-left-width: 1px;
	}
	.ui-datepicker-group-middle .ui-datepicker-header {
		border-right-width: 0;
		border-left-width: 1px;
	}
}
.ui-dialog {
	overflow: hidden;
	position: absolute;
	top: 0;
	left: 0;
	padding: .2em;
	outline: 0;
	.ui-dialog-titlebar {
		padding: .4em 1em;
		position: relative;
	}
	.ui-dialog-title {
		float: left;
		margin: .1em 0;
		white-space: nowrap;
		width: 90%;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	.ui-dialog-titlebar-close {
		position: absolute;
		right: .3em;
		top: 50%;
		width: 20px;
		margin: -10px 0 0;
		padding: 1px;
		height: 20px;
	}
	.ui-dialog-content {
		position: relative;
		border: 0;
		padding: .5em 1em;
		background: none;
		overflow: auto;
	}
	.ui-dialog-buttonpane {
		text-align: left;
		border-width: 1px 0 0 0;
		background-image: none;
		margin-top: .5em;
		padding: .3em 1em .5em .4em;
		.ui-dialog-buttonset {
			float: right;
		}
		button {
			margin: .5em .4em .5em 0;
			cursor: pointer;
		}
	}
	.ui-resizable-se {
		width: 12px;
		height: 12px;
		right: -5px;
		bottom: -5px;
		background-position: 16px 16px;
	}
}
.ui-menu {
	list-style: none;
	padding: 0;
	margin: 0;
	display: block;
	outline: none;
	.ui-menu {
		position: absolute;
	}
	.ui-menu-item {
		position: relative;
		margin: 0;
		padding: 3px 1em 3px .4em;
		cursor: pointer;
		min-height: 0;
		list-style-image: url("data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7");
	}
	.ui-menu-divider {
		margin: 5px 0;
		height: 0;
		font-size: 0;
		line-height: 0;
		border-width: 1px 0 0 0;
	}
	.ui-state-focus {
		margin: -1px;
	}
	.ui-state-active {
		margin: -1px;
	}
	.ui-icon {
		position: absolute;
		top: 0;
		bottom: 0;
		left: .2em;
		margin: auto 0;
	}
	.ui-menu-icon {
		left: auto;
		right: 0;
	}
}
.ui-menu-icons {
	position: relative;
	.ui-menu-item {
		padding-left: 2em;
	}
}
.ui-progressbar {
	height: 2em;
	text-align: left;
	overflow: hidden;
	.ui-progressbar-value {
		margin: -1px;
		height: 100%;
	}
	.ui-progressbar-overlay {
		background: url("data:image/gif;base64,");
		height: 100%;
		filter: alpha(opacity=25);
		opacity: 0.25;
	}
}
.ui-selectmenu-menu {
	padding: 0;
	margin: 0;
	position: absolute;
	top: 0;
	left: 0;
	display: none;
	.ui-menu {
		overflow: auto;
		overflow-x: hidden;
		padding-bottom: 1px;
		.ui-selectmenu-optgroup {
			font-size: 1em;
			font-weight: bold;
			line-height: 1.5;
			padding: 2px 0.4em;
			margin: 0.5em 0 0 0;
			height: auto;
			border: 0;
		}
	}
}
.ui-selectmenu-open {
	display: block;
}
.ui-selectmenu-button {
	display: inline-block;
	overflow: hidden;
	position: relative;
	text-decoration: none;
	cursor: pointer;
	span {
		&.ui-icon {
			right: 0.5em;
			left: auto;
			margin-top: -8px;
			position: absolute;
			top: 50%;
		}
		&.ui-selectmenu-text {
			text-align: left;
			padding: 0.4em 2.1em 0.4em 1em;
			display: block;
			line-height: 1.4;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
		}
	}
}
.ui-slider {
	position: relative;
	text-align: left;
	.ui-slider-handle {
		position: absolute;
		z-index: 2;
		width: 1.2em;
		height: 1.2em;
		cursor: default;
		-ms-touch-action: none;
		touch-action: none;
	}
	.ui-slider-range {
		position: absolute;
		z-index: 1;
		font-size: .7em;
		display: block;
		border: 0;
		background-position: 0 0;
	}
	&.ui-state-disabled {
		.ui-slider-handle {
			filter: inherit;
		}
		.ui-slider-range {
			filter: inherit;
		}
	}
}
.ui-slider-horizontal {
	height: .8em;
	.ui-slider-handle {
		top: -.3em;
		margin-left: -.6em;
	}
	.ui-slider-range {
		top: 0;
		height: 100%;
	}
	.ui-slider-range-min {
		left: 0;
	}
	.ui-slider-range-max {
		right: 0;
	}
}
.ui-slider-vertical {
	width: .8em;
	height: 100px;
	.ui-slider-handle {
		left: -.3em;
		margin-left: 0;
		margin-bottom: -.6em;
	}
	.ui-slider-range {
		left: 0;
		width: 100%;
	}
	.ui-slider-range-min {
		bottom: 0;
	}
	.ui-slider-range-max {
		top: 0;
	}
}
.ui-spinner {
	position: relative;
	display: inline-block;
	overflow: hidden;
	padding: 0;
	vertical-align: middle;
	.ui-icon {
		position: absolute;
		margin-top: -8px;
		top: 50%;
		left: 0;
	}
	.ui-icon-triangle-1-s {
		background-position: -65px -16px;
	}
	a.ui-spinner-button {
		border-top: none;
		border-bottom: none;
		border-right: none;
	}
}
.ui-spinner-input {
	border: none;
	background: none;
	color: inherit;
	padding: 0;
	margin: .2em 0;
	vertical-align: middle;
	margin-left: .4em;
	margin-right: 22px;
}
.ui-spinner-button {
	width: 16px;
	height: 50%;
	font-size: .5em;
	padding: 0;
	margin: 0;
	text-align: center;
	position: absolute;
	cursor: default;
	display: block;
	overflow: hidden;
	right: 0;
}
.ui-spinner-up {
	top: 0;
}
.ui-spinner-down {
	bottom: 0;
}
.ui-tabs {
	position: relative;
	padding: .2em;
	.ui-tabs-nav {
		margin: 0;
		padding: .2em .2em 0;
		li {
			list-style: none;
			float: left;
			position: relative;
			top: 0;
			margin: 1px .2em 0 0;
			border-bottom-width: 0;
			padding: 0;
			white-space: nowrap;
			&.ui-tabs-active {
				margin-bottom: -1px;
				padding-bottom: 1px;
				.ui-tabs-anchor {
					cursor: text;
				}
			}
			&.ui-state-disabled .ui-tabs-anchor {
				cursor: text;
			}
			&.ui-tabs-loading .ui-tabs-anchor {
				cursor: text;
			}
		}
		.ui-tabs-anchor {
			float: left;
			padding: .5em 1em;
			text-decoration: none;
		}
	}
	.ui-tabs-panel {
		display: block;
		border-width: 0;
		padding: 1em 1.4em;
		background: none;
	}
}
.ui-tooltip {
	padding: 8px;
	position: absolute;
	z-index: 9999;
	max-width: 300px;
	-webkit-box-shadow: 0 0 5px $color_silver_chalice_approx;
	//Instead of the line below you could use @include box-shadow($shadow-1, $shadow-2, $shadow-3, $shadow-4, $shadow-5, $shadow-6, $shadow-7, $shadow-8, $shadow-9, $shadow-10)
	box-shadow: 0 0 5px $color_silver_chalice_approx;
}
.ui-widget {
	font-family: Lato, Helvetica, Arial, sans-serif;
	font-size: 0.9em;
	.ui-widget {
		font-size: 1em;
	}
	input {
		font-family: Lato, Helvetica, Arial, sans-serif;
		font-size: 1em;
	}
	select {
		font-family: Lato, Helvetica, Arial, sans-serif;
		font-size: 1em;
	}
	textarea {
		font-family: Lato, Helvetica, Arial, sans-serif;
		font-size: 1em;
	}
	button {
		font-family: Lato, Helvetica, Arial, sans-serif;
		font-size: 1em;
	}
}
.ui-widget-content {
	border: 1px solid $color_pickled_bluewood_approx;
	background: $white url(images/ui-bg_spotlight_0_fff_600x600.png) 50% 2% repeat-x;
	color: $color_pickled_bluewood_approx;
	a {
		color: $color_pickled_bluewood_approx;
	}
	.ui-state-default {
		border: 1px solid $color_pickled_bluewood_approx;
		background: $color_pickled_bluewood_approx url(images/ui-bg_fine-grain_0_2c3e50_60x60.png) 50% 50% repeat;
		font-weight: normal;
		color: $white;
	}
	.ui-state-hover {
		border: 1px solid $color_pickled_bluewood_approx;
		background: $color_pickled_bluewood_approx url(images/ui-bg_fine-grain_0_34495e_60x60.png) 50% 50% repeat;
		font-weight: normal;
		color: $white;
	}
	.ui-state-focus {
		border: 1px solid $color_pickled_bluewood_approx;
		background: $color_pickled_bluewood_approx url(images/ui-bg_fine-grain_0_34495e_60x60.png) 50% 50% repeat;
		font-weight: normal;
		color: $white;
	}
	.ui-state-active {
		border: 1px solid $color_mountain_meadow_approx;
		background: $color_mountain_meadow_approx url(images/ui-bg_fine-grain_0_1ABC9C_60x60.png) 50% 50% repeat;
		font-weight: normal;
		color: $white;
	}
	.ui-state-highlight {
		border: 1px solid $color_submarine_approx;
		background: $color_athens_gray_approx url(images/ui-bg_fine-grain_0_ecf0f1_60x60.png) 50% 50% repeat;
