/**
 * Full Calendar Styles
 */
/* Remove that awful yellow color and border from today in Schedule */
.fc-state-highlight {
  font-weight: bold; }

/* Styling for each event from Schedule */
.fc-time-grid-event.fc-v-event.fc-event {
  border-radius: 0;
  border: none;
  padding: 5px;
  opacity: .65;
  left: 5% !important;
  right: 5% !important; }

/* Bolds the name of the event and inherits the font size */
.fc-event {
  border-radius: 0;
  font-weight: 500; }

.fc-content {
  padding: .25rem; }

/* Remove the header border from Schedule */
.fc td,
.fc th {
  border-top-width: 1px;
  border-left-width: 0;
  border-bottom-width: 0;
  border-right-width: 1px; }
  .fc td:last-child,
  .fc th:last-child {
    border-right-width: 0; }
  .fc td.fc-widget-header,
  .fc th.fc-widget-header {
    padding-top: .25rem;
    padding-bottom: .25rem;
    border-top-color: #0092D9;
    border-right-width: 0;
    border-bottom: 2px solid #0092D9; }

/* Inherits background for each event from Schedule. */
.fc-event .fc-bg {
  z-index: 1 !important;
  background: inherit !important;
  opacity: .25 !important; }

/* Normal font weight for the time in each event */
.fc-time-grid-event .fc-time {
  font-weight: normal !important; }

/* Apply same opacity to all day events */
.fc-ltr .fc-h-event.fc-not-end,
.fc-rtl .fc-h-event.fc-not-start {
  opacity: .65 !important;
  margin-left: 12px !important;
  padding: 5px !important; }

/* Apply same opacity to all day events */
.fc-day-grid-event.fc-h-event.fc-event.fc-not-start.fc-end {
  opacity: .65 !important;
  margin-left: 12px !important;
  padding: 5px !important; }

/* Material design button */
.fc-button {
  display: inline-block;
  position: relative;
  cursor: pointer;
  min-height: 36px;
  min-width: 88px;
  line-height: 36px;
  vertical-align: middle;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  text-align: center;
  border-radius: 0;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  outline: none;
  border: 0;
  padding: 0 6px;
  margin: 6px 8px;
  letter-spacing: .01em;
  background: transparent;
  color: currentColor;
  white-space: nowrap;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 14px;
  font-style: inherit;
  font-variant: inherit;
  font-family: inherit;
  text-decoration: none;
  overflow: hidden;
  -webkit-transition: box-shadow 0.4s cubic-bezier(0.25, 0.8, 0.25, 1), background-color 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: box-shadow 0.4s cubic-bezier(0.25, 0.8, 0.25, 1), background-color 0.4s cubic-bezier(0.25, 0.8, 0.25, 1); }

.fc-button:hover {
  background-color: rgba(158, 158, 158, 0.2); }

.fc-button:focus, .fc-button:hover {
  text-decoration: none; }

/* The active button box is ugly so the active button will have the same appearance of the hover */
.fc-state-active {
  background-color: rgba(158, 158, 158, 0.2); }

/* Not raised button */
.fc-state-default {
  box-shadow: none; }
  .fc-state-default.fc-corner-left, .fc-state-default.fc-corner-right {
    border-radius: 0; }

/* Tooltip */
.ui-tooltip {
  padding: 0 !important; }
  .ui-tooltip .ui-tooltip-titlebar {
    border: none; }
